body {
  margin: 0;
  height: 100vh;
  background-image: url('./assets/bg.png');
  background-size: cover;
  background-position: center;
  background-repeat: repeat;
  color: white;
  font-family: Arial, sans-serif;
}

.container {
  margin: 5vh;
  min-height: 93vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 12px;
  color: white;
  box-sizing: border-box;
}

.header-image {
  width: 100%;
  max-width: 530px;
  height: auto;
  margin-bottom: -20px;
  margin-top: -5vh;
  border-radius: 10px;
  margin-left: -20px;
}

.title {
  font-size: 2rem;
  margin-bottom: 30px;
  text-align: center;
}

.button1-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: -100px;
  align-items: center;
}

.button1 {
  margin-top: 50px;
  padding: 12px 24px;
  font-size: 25px;
  font-family:'Courier New', Courier, monospace;
  font-weight: bold;
  background: linear-gradient(180deg, #795740, #5e402a); 
  color: #fff8dc;
  border: 2px solid #d4b081;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s;
  transition: all 0.1s ease-in-out;
}

.button1:hover {
  background: linear-gradient(180deg, #543926, #2c1b13);
  transform: scale(1.05);
}

.button2-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: -100px;
  margin-left: 1100px;
}

.button2 {
  margin-top: 12vh;
  padding: 4px 12px;
  font-size: 15px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: #c2c2c2;
  color: #000000;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button2:hover {
  background-color: #565e64;
  transform: scale(1.05);
}
